import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import * as $ from 'jquery';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-eficiencia',
  templateUrl: './eficiencia.page.html',
  styleUrls: ['./eficiencia.page.scss'],
})
export class EficienciaPage implements OnInit {

  constructor(private translate : TranslateService) {
    this.translate.use(localStorage.getItem('idioma'));
   }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.graficaEficiencia();
    }
  ionViewDidLeave(){
      
    }

    graficaEficiencia() {
      var ultimox, ultimou;
      var chart;
      let num_tina = localStorage.getItem('idTina');
      let tequilera = localStorage.getItem('tequilera');
      let idiomas = localStorage.getItem('idioma');

      setTimeout(function () {
        if (idiomas === 'en') {
          Highcharts.charts.forEach(function (ch) {
            if (ch.options.title.text === 'Mediciones en Tiempo Real') {
              ch.setTitle({ text: 'Real-Time Measurements' });
              ch.xAxis[0].update({
                title: {
                  text: 'Measurement Time',
                },
              });
              ch.yAxis[0].update({
                title: {
                  text: 'Values %',
                },
              });
              ch.series[0].name = 'Efficiency';
            }
            ch.redraw();
          });
        }
        else if (idiomas === 'de') {
          Highcharts.charts.forEach(function (ch) {
            if (ch.options.title.text === 'Mediciones en Tiempo Real') {
              ch.setTitle({ text: 'Echtzeit-Messungen' });
              ch.xAxis[0].update({
                title: {
                  text: 'Zeitpunkt der Messung',
                },
              });
              ch.yAxis[0].update({
                title: {
                  text: 'Werte %',
                },
              });
              ch.series[0].name = 'Wirkungsgrad';
            }
            ch.redraw();
          });
        }
      },1000);
      
      $.ajax({ 
      url:'https://www.ininbio.com/pruebasLocalesFull/datos_Grafica.php?idTina='+num_tina+'&tequilera='+tequilera,
      type : "GET", 
      dataType : "json",
      success : function(datosGrafica){
      let eficiencia = [];
      $.each(datosGrafica, function(key,value){
      if (value.x){datosGrafica[key].x = parseInt(value.x);}
      if (value.u) {datosGrafica[key].u = parseFloat(value.u);}
  
      eficiencia.push([value.x, value.u]);
      }); 
    
     chart = Highcharts.chart('eficiencia',{
      chart: {
      type: 'spline',
      backgroundColor : 'transparent',
      },
      title: {
      text: 'Mediciones en Tiempo Real',
      align: 'center',
      style: {
        color: 'black'
    }
      },
      credits: {
        enabled: false
    },
      xAxis: {
      type: 'datetime',
      dateTimeLabelFormats: {
      second: '%H:%M:%S %P',
      },
      title: {
      text: 'Hora de Medici√≥n',
      style: {
        color: 'black'
    }
      },
      labels: {
        style: {
            color: 'black'
        }
    },
    gridLineColor: '#000000'
      },
      yAxis: [{
      title : {
        text : 'Valores %',
        style: {
          color: 'black'
      }
      },
      visible : true,
      plotLines: [{
      value: 0,
      width: 1
      }],
      labels: {
        style: {
            color: 'black'
        }
    },
    gridLineColor: 'black'
      }],
      tooltip: {
      formatter: function () {
      return '<b>' + this.series.name + '</b><br/>' +
      Highcharts.dateFormat('%H:%M:%S %P',this.x) + '<br/>' +
      Highcharts.numberFormat(this.y,2); 
      }
      },
      plotOptions: {
        spline: {
            marker: {
                enabled: true
            }
        }
    },
      legend: {
      enabled: false
      },
      exporting: {
        enabled: false,
        chartOptions: {
          plotOptions: {
              series: {
                  dataLabels: {
                      enabled: true
                  }
              }
          }
      },
      fallbackToExportServer: false
    },
      series: [{
      name: '¬∞Brix',
      type : "spline",
      yAxis : 0,
      data: eficiencia,
      color : '#1eff05'
      }]
      });
      }
      });
      setInterval(function () {
      $.get("https://www.ininbio.com/pruebasLocalesFull/datos_Grafica.php?tinaIndividual="+num_tina+"&tequilera="+tequilera+"&Consultar=1", function(UltimosDatos) {
      var varlocalx=parseInt(UltimosDatos[0].x);
      var varlocalu=parseFloat(UltimosDatos[0].u);
      
      if((getx()!=varlocalx)&&(getu()!=varlocalu)){
      
      chart.series[0].addPoint([varlocalx, varlocalu]);
      }
      });}, 1000);
      function getx(){return ultimox;}
      function getu(){return ultimou;}
      }

    refrescar(){
      window.location.assign('/charts');
    }

}
